
import types from "@/utils/types";
import { debounce } from "lodash";
import { defineComponent, onMounted, computed, ref, reactive, watchEffect } from "vue";

export default defineComponent({
    name: "FoxPreviewImage",
    props: {
        modelValue: {
            type: Boolean,
            default: false,
        },
        src: [String, Array],
        zIndex: {
            type: Number,
            default: 9000,
        },
    },
    setup(props, { emit }) {
        let flag = ref<boolean>(false);
        let status = ref<number>(0);
        let active = ref<number>(0);
        let angle = ref<number>(0);
        let scale = ref<number>(1);
        let cacheX = ref<number>(0);
        let cacheY = ref<number>(0);
        let x = ref<number>(0);
        let y = ref<number>(0);
        let uri = ref<Array<string>>([]);
        let startLocation = reactive({
            x: 0,
            y: 0,
        });

        const init = () => {
            flag.value = props.modelValue;
        };
        const close = () => {
            flag.value = false;
            emit("update:modelValue", flag.value);
        };

        const move = (e: MouseEvent) => {
            if (status.value === 1) {
                const { x: mouseX, y: mouseY } = e;
                // 鼠标的移动距离
                const mvX = mouseX - startLocation.x;
                const mvY = mouseY - startLocation.y;
                // x.value = x.value + mvX - cacheX.value;
                // y.value = y.value + mvY - cacheY.value;
                x.value = mvX + x.value - cacheX.value;
                y.value = mvY + y.value - cacheY.value;
                cacheX.value = mvX;
                cacheY.value = mvY;
            }
        };

        /**
         * 缩小
         */
        const zoomOut = () => {
            if (scale.value > 0.5) {
                scale.value -= 0.1;
            }
        };
        /**
         * 放大
         */
        const enlarge = () => {
            if (scale.value < 2) {
                scale.value += 0.1;
            }
        };
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const mousewheel = (ev: any) => {
            const isUp = (ev.wheelDelta || ev.detail * -40) > 0;
            if (isUp) {
                enlarge();
            } else {
                zoomOut();
            }
        };

        const handleMouseMove = debounce(move, 2);
        const handleMousewheel = debounce(mousewheel, 30);

        const mouseup = () => {
            status.value = 0;
            cacheX.value = 0;
            cacheY.value = 0;
        };
        const mousedown = (e: MouseEvent) => {
            status.value = 1;
            startLocation = { x: e.x, y: e.y };
        };

        /**
         * 顺时针旋转
         */
        const clockwiseRotation = () => {
            angle.value += 90;
        };
        /**
         * 逆时针旋转
         */
        const anticlockwiseRotation = () => {
            angle.value -= 90;
        };

        /**
         * 获取滚动条的宽度
         */
        const getScrollWidth = () => {
            const scroll = document.createElement("div");
            const scrollIn = document.createElement("div");
            scroll.appendChild(scrollIn);
            scroll.style.width = "100px";
            scroll.style.height = "50px";
            scroll.style.overflow = "scroll";
            scroll.style.marginLeft = "-100000px";
            document.body.appendChild(scroll);
            const scrollInWidth = scrollIn.offsetWidth;
            const scrollWidth = scroll.offsetWidth;
            const tmp = setTimeout(() => {
                document.body.removeChild(scroll);
                clearTimeout(tmp);
            }, 10);
            return scrollWidth - scrollInWidth;
        };
        const prev = () => {
            const len = uri.value.length || 0;
            if (active.value > 0) {
                active.value--;
            } else {
                active.value = len - 1;
            }
            angle.value = 0;
            scale.value = 1;
            x.value = 0;
            y.value = 0;
            startLocation.x = 0;
            startLocation.y = 0;
            cacheX.value = 0;
            cacheY.value = 0;
        };
        const next = () => {
            const len = uri.value.length || 0;
            if (active.value < len - 1) {
                active.value++;
            } else {
                active.value = 0;
            }
            angle.value = 0;
            scale.value = 1;
            x.value = 0;
            y.value = 0;

            startLocation.x = 0;
            startLocation.y = 0;
            cacheX.value = 0;
            cacheY.value = 0;
        };

        const getCurrScale = computed(() => {
            return parseFloat(scale.value.toFixed(1));
        });

        onMounted(() => {
            init();
        });

        watchEffect(() => {
            flag.value = props.modelValue;
            console.log(flag.value);
            if (props.modelValue) {
                document.body.style.paddingRight = getScrollWidth() + "px";
                document.body.style.overflow = "hidden";
            } else {
                document.body.setAttribute("style", "");
            }
        });

        watchEffect(() => {
            const type = types(props.src);
            active.value = 0;
            if (type === "string") {
                angle.value = 0;
                scale.value = 1;
                x.value = 0;
                y.value = 0;
                startLocation.x = 0;
                startLocation.y = 0;
                cacheX.value = 0;
                cacheY.value = 0;

                uri.value = [props.src as string];
            } else if (type === "array") {
                uri.value = props.src as string[];
                angle.value = 0;
                scale.value = 1;
                x.value = 0;
                y.value = 0;
                startLocation.x = 0;
                startLocation.y = 0;
                cacheX.value = 0;
                cacheY.value = 0;
                uri.value = props.src as string[];
            }
        });

        return {
            flag,
            status,
            active,
            uri,
            scale,
            angle,
            x,
            y,
            handleMouseMove,
            handleMousewheel,
            /**methods */
            close,
            prev,
            next,
            mouseup,
            mousedown,
            zoomOut,
            enlarge,
            anticlockwiseRotation,
            clockwiseRotation,
            /**computed */
            getCurrScale,
        };
    },
});
